import React from 'react';
import Link from 'gatsby-link';
import Img from 'gatsby-image';
import theme from '../theme';

const IndexPage = ({ data }) => (
  <div className="hero">
  	<Img 
  		alt="Norwich Castle"
  		sizes={data.headerImage.sizes}
  		style={{
		    position: "absolute",
		    left: 0,
		    top: 0,
		    width: "100%",
		    height: "100%"
		  }}
  	/>
  	<div className="content">
        <h2>Castle Electrical Services Ltd</h2>
    	<h1>Norwich Electrical Contractors</h1>
    	<p>Qualified Electricians carrying out all types of Electrical Wiring, Electrical Testing, covering Norwich, Norfolk, Suffolk and East Anglia.</p>
    </div>
    <style jsx>{`
    	.hero {
    		display: block;
    		position: relative;
    		height: 400px;
    	}

    	.hero:after {
    		display: block;
    		position: absolute;
    		content: '';
    		left: 0;
    		right: 0;
    		top: 0;
    		bottom: 0;
    		z-index: 1;
    		background-color: rgba(0,0,0,0.4);
    	}
    	.content {
    		position: absolute;
    		z-index: 3;
    		color: ${theme.colors.white};
    		left: 50%;
    		top: 50%;
    		transform: translate(-50%,-50%);
    	}

    	.content h1 {
    		text-transform: uppercase;
    		font-size: 1.5em;
    		line-height: 1;
    		margin: 0;
    	}

        .content h2 {
            font-size: 1em;
            display: inline-block;
            padding: .5em .4em;
            border-radius: .4em;
            background-color: rgba(255,255,255,0.5);
            color:${theme.colors.red};
            margin-bottom: 1em;
        }

    	.content p {
    		font-weight: normal;
    		margin-top: 1em;
    		font-size: 14px;
    	}

    	@media(min-width: ${theme.breakpoints.md}px) {
    		.content h1 {
    			font-size: 2em;
    		}

    		.content p {
    			font-size: 1em;
    		}
    	}

    	@media(min-width: ${theme.breakpoints.lg}px) {
    		.hero {
    			height: 100vh;
    		}
    		.content h1 {
    			font-size: 3.5em;
    		}

    		.content p {
    			font-size: 1.2em;
    		}
    	}
    `}</style>
  </div>
)

export default IndexPage

export const pageQuery = graphql`
  query FrontPageQuery {
    headerImage: imageSharp(id: { regex: "/norwich-castle/" }) {
      sizes(maxWidth: 1240) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`



